<template>
  <div class="login">
    <van-nav-bar title="会员登录" left-arrow @click-left="$router.go(-1)" />
    <div class="container">
      <div class="title">
        <h3>手机号登录</h3>
        <p>未注册的手机号登录后将自动注册</p>
      </div>

      <div class="form">
        <div class="form-item">
          <input v-model="phone" class="inp" maxlength="11" placeholder="请输入手机号码" type="text">
        </div>
        <div class="form-item">
          <input v-model="picCode" class="inp" maxlength="5" placeholder="请输入图形验证码" type="text">
          <img v-if="picUrl" :src="picUrl" @click="updatePic" alt="">
        </div>
        <div class="form-item">
          <input v-model="messageCode" class="inp" placeholder="输入短信验证码，默认为246810" type="text">
          <button @click="getMsgCode">{{timer?`${count}秒后重新发送`:'获取验证码'}}</button>
        </div>
      </div>

      <div @click="Login" class="login-btn">登录</div>
    </div>
  </div>
</template>

<script>
import { getPicCodeApi, getMsgCodeApi, LoginApi } from '@/api/login'
import { Toast } from 'vant'
export default {
  name: 'LoginPage',
  data () {
    return {
      // 图形验证码图片地址
      picUrl: '',
      // 图形验证码key
      picKey: '',
      phone: '',
      // input中获取到的图像验证码
      picCode: '',
      // input中获取到的短信验证码
      messageCode: '',
      // 倒计时时间
      second: 60, // 用于重置
      count: 60, // 用于倒计时计数
      timer: null
    }
  },
  created () {
    this.getPicCode()
  },
  methods: {
    async getPicCode () {
      const { data } = await getPicCodeApi()
      this.picUrl = data.base64
      this.picKey = data.key
    },
    updatePic () {
      this.getPicCode()
    },
    // 获取验证码时要检查输入的号码和图形验证码的格式是否正确
    checkFormat () {
      if (!/^1[3-9]\d{9}$/.test(this.phone) || !/^\w{4}$/.test(this.picCode)) {
        Toast('请输入正确的手机号和图形验证码')
        return false
      }
      return true
    },
    //  || /^\d{6}$/.test(this.messageCode)
    async getMsgCode () {
      // 先判断下格式
      if (!this.checkFormat()) return
      // 设置倒计时，并防止多次点击触发多次
      if (this.timer) return
      this.timer = setInterval(() => {
        if (this.count > 0) {
          this.count--
        } else {
          // console.log(222)
          setInterval(this.timer)
          this.timer = null
          this.count = this.second
        }
      }, 1000)
      await getMsgCodeApi(this.picCode, this.picKey, this.phone)
      Toast('短信发送成功，请等待接收')
    },
    async Login () {
      // 先判断下格式
      if (!this.checkFormat()) return
      if (!/^\d{6}$/.test(this.messageCode)) {
        Toast('请输入正确的短信验证码')
        return
      }
      const res = await LoginApi(this.phone, this.messageCode)
      Toast('登陆成功')
      this.$store.commit('user/setUserInfo', res.data)
      // 判断是否有回跳地址
      if (this.backUrl) {
        this.$router.replace(this.backUrl)
      } else { this.$router.push('/') }
    }
  },
  // 离开页面销毁定时器
  destroyed () {
    clearInterval(this.timer)
  },
  computed: {
    backUrl () {
      return this.$route.query.backUrl
    }
  }

}
</script>

<style lang="less" scoped>
.container {
  padding: 49px 29px;

  .title {
    margin-bottom: 20px;
    h3 {
      font-size: 26px;
      font-weight: normal;
    }
    p {
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }

  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }
    img {
      width: 94px;
      height: 31px;
    }
    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg,#ecb53c,#ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0,0,0,.1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
